<template>
  <!-- 图片容器，用于展示所有图片 -->
  <div class="image-container">
    <!-- 使用 v-for 循环渲染每一张图片 -->
    <div class="backg1" ref="backg1"></div>
    <div class="backg2" ref="backg2"></div>
    <img
      v-for="(image, index) in images"
      :key="index"
      :src="getImageUrl(image.imgUrl)"
      :alt="image.alt"
      :class="{ normal: index === activeIndex }"
      :style="getImageStyle(index)"
      @mouseenter="handleMouseEnter(index)"
      @mouseleave="handleMouseLeave"
      @click="clickImg(image)"
    />
  </div>
</template>

<script>
// 导入图片数据
import imgData from "@/assets/data/imgdata.json";

export default {
  data() {
    return {
      // 从 JSON 文件中加载图片数据
      images: imgData,
      // 存储当前激活图片的索引，初始值为 0（即第一张图片）
      activeIndex: 0,
    };
  },
  methods: {
    clickImg(image) {
      // 将 image 存储到 sessionStorage 中
      sessionStorage.setItem("data", JSON.stringify(image));

      // 判断 imgList 数组是否有值
      if (image && image.imgList && image.imgList.length > 0) {
        if (image.imgList.length == "3") {
          // 如果 imgList 数组有值，跳转到 /imgIndex
          this.$router.push("/imgIndex");
        } else {
          this.$router.push("/imgIndex2");
        }
      } else {
        // 如果 imgList 数组没有值，跳转到 /indexText
        this.$router.push("/indexText");
      }
    },
    // 用于动态加载图片
    getImageUrl(imgPath) {
      // 在 Vue 项目中，通过 require() 动态加载图片log
      return require(`@/assets/${imgPath}`);
    },

    // 鼠标进入图片时更新 activeIndex 为当前图片的索引
    handleMouseEnter(index) {
      this.activeIndex = index;
    },

    // 鼠标离开时，如果需要，可以恢复为默认状态
    handleMouseLeave() {
      // this.activeIndex = 0; // 可选择恢复为第一个图片的状态
    },

    // 根据图片索引返回对应的样式
    getImageStyle(index) {
      console.log();
      this.$nextTick(() => {
        if (this.$refs.backg1) {
          if (this.activeIndex <= 3) {
            this.$refs.backg1.style.display = "block";
          } else {
            this.$refs.backg1.style.display = "none";
          }
        }
        if (this.$refs.backg2) {
          if (this.activeIndex >= this.images.length - 4) {
            this.$refs.backg2.style.display = "block";
          } else {
            this.$refs.backg2.style.display = "none";
          }
        }
      });
      const distance = 6; // 每张图片的位移量
      const opacityBase = 1; // 选中图片的透明度
      const zIndexBase = 10; // 选中图片的 z-index 值

      // 计算当前图片与激活图片的偏移量
      const offset = Math.abs(index - this.activeIndex);
      // 根据偏移量递减透明度
      const opacity = opacityBase - offset * 0.2;
      // 根据偏移量递减 z-index
      const zIndex = zIndexBase - offset;

      // 如果当前图片在激活图片的左侧，向左位移
      if (index < this.activeIndex) {
        const leftOffset = offset * distance;
        return {
          transformStyle: "preserve-3d",
          transform: `translateX(-${leftOffset}rem)  translateX(-90%) rotateY(80deg) translateY(-50%)`,
          // opacity: opacity,
          zIndex: zIndex, // 设置 z-index 以控制层级关系
          boxShadow:
            index !== this.activeIndex
              ? "0 4px 10px rgba(0, 0, 0, 0.2)"
              : "none", // 非选中图片添加阴影
        };
      } else if (index > this.activeIndex) {
        // 如果当前图片在激活图片的右侧，向右位移
        const rightOffset = offset * distance;
        return {
          transformStyle: "preserve-3d",
          transform: `translateX(${rightOffset}rem)  translateX(5%)  translateX(-15%) rotateY(-80deg) translateY(-50%)`,
          // opacity: opacity,
          zIndex: zIndex,
          boxShadow:
            index !== this.activeIndex
              ? "0 4px 10px rgba(0, 0, 0, 0.2)"
              : "none",
        };
      }

      // 当前图片为激活图片时，不做位移，保持原尺寸，透明度为 1，z-index 为最大值
      return {
        transformStyle: "preserve-3d",
        transform: "rotateY(0deg) translateX(-50%) scale(1) translateY(-50%) ", // 中心图片保持原大小
        // opacity: opacityBase,
        zIndex: zIndexBase,
        width: "20%",
        height: "66%",
        boxShadow: "none", // 选中图片不显示阴影
      };
    },
  },
};
</script>

<style scoped>
/* 图片容器样式 */
.image-container {
  transform-style: preserve-3d; /* 保持 3D 效果 */
  margin-top: 0;
  position: relative; /* 设置为相对定位，以便控制子元素的绝对定位 */
  width: 100%; /* 容器宽度为 100% */
  height: 100%; /* 设置固定高度，避免容器缩放 */
  overflow: hidden; /* 隐藏超出的部分 */
  background: #f2f2f2; /* 容器的背景色 */
  perspective: 3400px; /* 添加透视效果，视距为 1200px */
}

.backg1 {
  background: url("../assets/back/back.png");
  background-size: 100% 100%;

  width: 30%;
  height: 30%;
  position: absolute; /* 使用绝对定位 */
  top: 50%; /* 设置图片距离顶部的距离 */
  left: 20%; /* 图片水平居中 */
  transform: translateX(-50%) translateY(-50%); /* 水平居中图片 */
}
.backg2 {
  background: url("../assets/back/back.png");
  background-size: 100% 100%;
  width: 30%;
  height: 30%;
  position: absolute; /* 使用绝对定位 */
  top: 50%; /* 设置图片距离顶部的距离 */
  right: -10%; /* 图片水平居中 */
  transform: translateX(-50%) translateY(-50%); /* 水平居中图片 */
}

/* 图片样式 */
img {
  width: 24%; /* 设置图片宽度 */
  height: 60%; /* 设置图片高度 */
  position: absolute; /* 使用绝对定位 */
  top: 50%; /* 设置图片距离顶部的距离 */
  left: 50%; /* 图片水平居中 */
  transform-style: preserve-3d; /* 保持 3D 效果 */
  transform: translateX(-50%) translateY(-50%); /* 水平居中图片 */
  transition: transform 0.5s ease, opacity 0.5s ease, box-shadow 0.5s ease; /* 添加平滑过渡效果 */
}

/* 激活的图片添加 normal 类，设置较高的 z-index */
.normal {
  z-index: 10; /* 激活的图片显示在最上层 */
}

</style>


