import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: () => import("../components/HelloWorld.vue"),
    },
    {
      path: '/saolian',
      name: 'saolian',
      component: () => import("../components/Saolian.vue"),
    },
    {
      path: '/imgIndex',
      name: 'imgIndex',
      component: () => import("../components/imgIndex.vue"),
    },
    {
      path: '/indexText',
      name: 'indexText',
      component: () => import("../components/indexText.vue"),
    },
    {
      path: '/imgIndex2',
      name: 'imgIndex2',
      component: () => import("../components/imgIndex2.vue"),
    },
  ]
})



